define(['lodash'], function (_) {
    'use strict';

    const compsToAlwaysShow = ['SITE_HEADER', 'SITE_FOOTER'];

    return function shouldShowFixedPositionComp(argsObj) {
        if (_.includes(compsToAlwaysShow, argsObj.compId)) {
            return true;
        }

        const isFixedPosition = _.get(argsObj.compLayout, 'fixedPosition');
        const showFixedComponents = argsObj.renderFlags.allowShowingFixedComponents;
        return showFixedComponents || !isFixedPosition;
    };
});
